.great-shadow {
  position: fixed;
  display: flex;
  justify-content: flex-end;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(5, 23, 58, 0.4);
  z-index: 1100;
  transition: all 200ms ease;
  opacity: 0;
  visibility: hidden;
}
.great-shadow.show {
  opacity: 1;
  visibility: visible;
}

.modal {
  position: absolute;
  top: 0;
  right: 0;
  max-width: 492px;
  height: 100%;
  width: 100%;
  background-color: var(--light);
  padding: 40px;
  padding-right: 10px;
  padding-top: 70px;
  box-sizing: border-box;
  overflow: hidden;
  transition: all 300ms ease;
  transform: translateX(105%);
}
.modal.show {
  transform: translateX(0);
}
.modal .modal__close-btn {
  position: absolute;
  top: 30px;
  right: 34px;
  width: 20px;
  height: 20px;
  background-color: transparent;
}
.modal .modal__close-btn::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--border);
  border-radius: 3px;
  transform: translateY(-50%) rotate(45deg);
  transition: var(--great-transition);
}
.modal .modal__close-btn::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--border);
  border-radius: 3px;
  transform: translateY(-50%) rotate(-45deg);
  transition: var(--great-transition);
}
.modal .modal__close-btn:hover::before, .modal .modal__close-btn:hover::after {
  background-color: var(--green);
}
.modal .modal__title {
  margin-bottom: 25px;
  font-size: 22px;
  line-height: 1.2;
  font-weight: 700;
}
.modal .modal__header {
  margin-bottom: 48px;
}
.modal .modal__header p {
  margin-top: 20px;
  margin-bottom: 0;
  font-size: 15px;
  color: var(--gray-text);
}
.modal .modal__form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.modal .modal__form .btn {
  width: 100%;
}
.modal .modal__form .modal__warning {
  margin: 0;
  text-align: center;
  font-size: 13px;
  color: var(--grey-text);
}
.modal .modal__content-container {
  position: relative;
  height: 100%;
  padding-right: 30px;
  overflow: auto;
  box-sizing: border-box;
}
