  min-height: 140px;
}

.loading {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.5411764706);
  opacity: 0;
  visibility: hidden;
  transition: var(--great-transition);
}
.loading .spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.loading--show {
  opacity: 1;
  visibility: visible;
}

.success-message, .error-message {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 3rem;
  background-color: var(--light);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
  font-size: 2rem;
  color: var(--dark-green);
  box-sizing: border-box;
  transition: var(--great-transition);
  opacity: 0;
  visibility: hidden;
}
.success-message p, .error-message p {
  margin: 0;
}
.success-message--show, .error-message--show {
  opacity: 1;
  visibility: visible;
}

.error-message {
  color: var(--alert-color);
}

